@import url(https://fonts.googleapis.com/css?family=Playfair+Display+SC:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.england{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding: 0.1rem;
    grid-gap: 0.1rem;
    gap: 0.1rem;
  
    max-width: 50vw;
    overflow: auto;
    background-color: black;
    margin: 1rem auto;
    border-radius: 25px;
    max-height: 130vh;
    overflow: scroll;
   
}
.soccer{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    padding: 1rem;
  
 
    background-image: url("http://www.clker.com/cliparts/i/l/M/o/r/l/soccer-field-hi.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
   
} 

.soccer div{
    width: 200px;
    padding: 0;
    margin: 3rem 0 0 0;
    background-color: transparent;
}
.soccer img {
 
    height: 150px;
    width: 150px;
    border-radius: 50%;
    margin-left: 40px;
   
}


.players{
    min-height: 200px;
    text-align: center;
    padding: 1rem;
    background-color:#eee;
    max-height: 100vh;
    
}

.players img{
    width: 200px;
    height: 200px;
    object-fit: cover;

}



h1{
    text-align: center;
    
}
.filter {
    text-align: center;
    padding: 1rem;
    background-color:#eee;
   
}

.filter input{
    height: 25px;
    width: 200px;
    border: solid 2px rgb(163, 8, 8);
    font-size: 1rem;
    border-radius: 25px;
    text-align: center;
}
.App {
  background-color: #eee;

}
.spill {
  
  position: relative;
  min-height: 100vh;
  min-width: 100vw;
  background-color: rgb(216, 34, 34);
  background-image: url("https://i.pinimg.com/originals/ba/94/4e/ba944e532e51f966700821688795a1e5.jpg");

  background-repeat: no-repeat;

 

}

h1{
  background-color: rgb(216, 34, 34);
  height: 40px;
  margin: 0;
  color: white;
  font-family: 'Playfair Display SC', serif;
  font-weight: 700;
  font-size: 2rem;
  padding: 1rem;
  letter-spacing: 0.1rem;
}

h2{
  text-align: center;
  text-transform: uppercase;
  font-weight: 150;
  font-family: 'Playfair Display SC', serif;
}

.players:hover{
  background-color: rgb(173, 173, 173);
  cursor: pointer;
}

.soccer .players:hover{
  background-color: none;
}


